// USER VARIABLES SECTION
$fontsize: 16px;
$system: -apple-system, BlinkMacSystemFont, Arial, sans-serif;
$notosans: NotoSans, sans-serif;
$lora: Lora, sans-serif;
$opensans: OpenSans, sans-serif;
$georgia: Georgia, sans-serif;

// Bootstrap variables
$grid-columns: 24;
$grid-gutter-width: 0px;
$grid-row-columns: 6;



// FONTS LOAD SECTION

@mixin font($fname, $fstyle, $fweight, $furl) {
	@font-face {
		font-family: $fname;
		font-style: $fstyle;
		font-weight: $fweight;
		src: local($fname), url($furl) format('woff2'); } }

@include font('NotoSans', normal, 400, '/fonts/notosans-regular-webfont.woff2');
@include font('Lora', italic, 400, '/fonts/lora-italic-webfont.woff2');
@include font('OpenSans', normal, 300, '/fonts/opensans-light-webfont.woff2');
@include font('Georgia', normal, 400, '');

input, textarea {
	outline: none;
	&:focus:required:invalid {
		border-color: red; }
	&:required:valid {
		border-color: green; } }

body {
	font-family: $lora;
	font-size: $fontsize;
	min-width: 320px;
	position: relative;
	line-height: 1.65;
	overflow-x: hidden; }
