@import "config";
@import "../../../node_modules/custom-build/dist/mmenu.css";
@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/bootstrap-reboot";
@import "node_modules/bootstrap/scss/utilities";
@import "node_modules/bootstrap/scss/grid";
@import "node_modules/bootstrap/scss/modal";
@import "node_modules/bootstrap/scss/forms";
@import "../../../node_modules/mburger-css/dist/mburger.css";
@import "../../../node_modules/owl.carousel/dist/assets/owl.carousel.min.css";

* {
  li {
    list-style: none; } }



.aside {
  height: 100vh;
  background-image: url(/img/aside_bg.jpg); }


.main_logo {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover; }
  padding: 30px 35px 30px 35px;
  margin-bottom: 6vh; }

.nav_main {
  font-family: $opensans;
  font-weight: 300;
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 6vh;
  a {
    padding-bottom: 37px;
    display: inline-block;
    color: #f7f7f7;
    text-decoration: none; } }
.link-border::after {
    display: block;
    width: 100%;
    content: '';
    opacity: 0.48;
    border-bottom: 1px solid #686464;
    position: absolute;
    left: 0;
    padding-bottom: 16px; }
.caret {
  position: absolute;
  width: 30px;
  height: 30px;
  right: 0;
  text-align: center;
  padding: 2px 15px;
  content: ' ';
  color: #f0f0f0;
  font-size: 12px;
  background-image: url(/img/caret.png);
  background-repeat: no-repeat; }

.nav_add {
  font-family: $opensans;
  font-weight: 300;
  line-height: 2;
  letter-spacing: normal;
  text-align: left;
  color: #bdbdbd;
  margin-bottom: 3vh;
  a {
    text-decoration: none;
    color: #bdbdbd; } }

.aside_callback {

  padding-left: 38px;
  a {
    font-family: $opensans;
    font-size: 20px;
    font-weight: 300;
    line-height: 0.9;
    text-align: left;
    color: #fefefe;
    text-decoration: none; }
  button {
    color: #c57c40;
    border: none;
    padding: 0;
    background-color: transparent;
    border-bottom: 1px dashed #c57c40; } }

.main {
  height: auto; }

.main_body, .main_carousel {
  position: relative; }

.main_body {
  margin-left: 90px;
  position: absolute;
  top: 75vh;
  z-index: 10; }
.main_body_text {
  background-color: rgba(22%, 19%, 17%, 0.5);
  text-transform: uppercase;
  padding: 5px 34px 16px 13px;
  h1 {
    font-family: $opensans;
    position: relative;
    font-size: 2em;
    text-align: left;
    color: #f0f0f0;
    font-weight: normal;
    text-transform: uppercase;
    line-height: 0.976;
    text-indent: 2px; }


  h2 {
    font-family: $opensans;
    position: relative;
    font-weight: normal;
    font-size: 1.5em;
    line-height: 1.57;
    text-align: left;
    color: #f0f0f0; } }


.main_body_text::before {
  content: "";
  position: absolute;
  top: 40px;
  left: 30px;
  height: 75%;
  width: 100%;
  background-color: rgba(45, 38, 30, 0.3); }

.main_body_btn {
  margin-left: 13px;
  padding-left: 23px;
  position: relative;
  background-color: #b36060;
  width: 200px;
  height: 40px;
  border: none;
  font-family: $lora;
  font-size: 1.2rem;
  font-style: italic;
  line-height: 0.78;
  letter-spacing: normal;
  text-align: left;
  color: #fefefe; }

.main_body_btn::after {
  display: inline-block;
  position: absolute;
  right: 16px;
  top: 12px;
  content: url(/img/arrow.png); }

.slide {
  img {
    height: 100vh; } }

.owl-dots {
  position: absolute;
  top: 95vh;
  left: 90px;
  z-index: 5; }
.owl-dot {
  span {
    width: 10px;
    height: 10px;
    content: url(/img/dot.png);
    margin: 5px 7px;
    background: #D6D6D6 transparent;
    display: block;
    border-radius: 50px; } }

.owl-dot.active span {
  height: 16px;
  width: 16px;
  content: url(/img/dot-active.png); }

.navbar {
  padding: 12px 0;
  height: 50px;
  background-image: url(/img/tablet_navbar.png); }
.navbar_left {
  a {
    text-decoration: none;
    color: white; }
  img {
    margin: -4px auto 0; } }
.navbar_right {
  margin-right: 15px; }

.mburger {
  margin: -6px auto 0;
  width: 1px;
  height: 1px;
  --mb-bar-height: 2px; }

.mm-navbar__title {
  background-color: #dfddd8;
  font-family: $notosans;
  font-size: 13px;
  font-weight: 600;
  color: #4a4a4a !important;
  text-transform: uppercase;
  line-height: 1.846;
  text-align: center; }

.modal-body {
  padding: 0;
  background-color: #f2f2f2; }

.modal_form {
  padding-top: 48px;
  width: 50%;
  margin-left: 50px; }

.form_title {
  text-transform: uppercase;
  font-family: $notosans;
  font-size: 1em;
  line-height: 1;
  color: #4a4a4a;
  margin-bottom: 38px; }

#close_modal {
  border: none;
  color: #f2f2f2;
  float: right;
  margin: -35px 10px 0px 0px; }

.form-group {
  label {
    width: 100%;
    font-family: FiraSans;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.57;
    letter-spacing: normal;
    text-align: left;
    color: #716d6d;
    span {
      color: #b25252; } }
  input {
    height: 45px;
    width: 90%;
    border: none; }
  input::not(:last-child) {
    margin: 0px 60px 55px 0px; }
  textarea {
    width: 90%;
    height: 105px;
    border: none;
    resize: none; } }

.form_btn-submit {
  float: right;
  margin-right: 40px;
  font-family: $lora;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: italic;
  line-height: 1.29;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  width: 216px;
  height: 41px;
  border-radius: 2px;
  background-color: #db7d3a;
  border: none; }


@media (min-width: 1950px) {
  body {
    max-width: 1940px;
    width: 100%;
    padding-right: 0px;
    padding-left: 0px;
    margin-right: auto;
    margin-left: auto;
    background-image: url(/img/aside_bg.jpg); } }

@media (min-width: 1201px) and (max-width: 1400px) {
  .aside {
    font-size: 0.9em; }
  .nav_main {
    margin-bottom: 5vh;
    a {
      padding-bottom: 20px; } }
  .link-border::after {
    padding-bottom: 10px; }
  .aside_callback {
    a {
      font-size: 0.9em; } } }

@media (min-width: 768px) and (max-width: 1200px) {
  .navbar {
    .row {
      width: 100vw; } }
  .content {
    background-image: url(/img/tablet_bg.jpg);
    background-size: cover;
    height: 100vh; }
  .main_body {
    margin-left: 90px;
    position: absolute;
    top: 520px;
    z-index: 10; }
  .main_body_text {
    text-transform: uppercase;
    padding: 5px 34px 16px 13px;
    h1 {
      font-size: 1.5em; }
    h2 {
      font-size: 1em; } }
  .main_body_text::before {
    top: 40px;
    left: 30px;
    height: 75%;
    width: 100%;
    background-color: rgba(45, 38, 30, 0.3); }
  .main_body_btn {
    margin-left: 13px;
    padding-left: 23px;
    width: 150px;
    height: 40px;
    font-family: $lora;
    font-size: 0.9rem; }
  .main_body_btn::after {
    display: inline-block;
    position: absolute;
    right: 16px;
    top: 12px; }

  .slide {
    img {
      height: 25%;
      zoom: 0.7; } }

  .owl-dots {
    top: 665px; } }

@media (max-width: 767px) {
  .navbar {
    background: #3c3a3a;
    height: auto; }
  .navbar_left {
    img {
      height: auto;
      padding-left: 8px; } }
  .navbar_right {
    height: 100%;
    margin-right: 15px; }
  .content {
    background-image: url(/img/tablet_bg.jpg);
    background-size: cover;
    height: 100vh; }
  .main_body {
    margin-left: 35px;
    top: 330px; }
  .main_body_text {
    padding: 5px 5px 1px 5px;
    h1 {
      font-size: 0.9em;
      line-height: 1; }
    h2 {
      font-size: 0.7em;
      line-height: 1; } }

  .main_body_btn {
    padding-left: 10px;
    width: 80px;
    height: 20px;
    font-size: 0.6rem; }
  .main_body_btn::after {
    right: 5px;
    top: 4px;
    zoom: 0.8; }
  .slide {
    img {
      height: auto;
      zoom: 0.4; } }
  .owl-dots {
    top: 410px;
    left: 10px; }
  .owl-dot {
    span {
      width: 5px;
      height: 5px; } }
  .owl-dot.active span {
    height: 8px;
    width: 8px; } }


@media (max-width: 480px) {

  .navbar {
    background: #3c3a3a;
    height: auto; }
  .navbar_left {
    img {
      height: auto;
      padding-left: 8px; } }
  .navbar_right {
    height: 100%;
    margin-right: 15px; }
  .content {
    background-image: url(/img/tablet_bg.jpg);
    background-size: cover;
    height: 100vh; }
  .main_body {
    margin-left: 20px;
    top: 230px; }
  .main_body_text {
    padding: 5px 5px 1px 5px;
    h1 {
      font-size: 0.8em;
      line-height: 1; }
    h2 {
      font-size: 0.6em;
      line-height: 1; } }
  .main_body_text::before {
    height: 60%;
    width: 100%; }
  .main_body_btn {
    margin-left: 13px;
    padding-left: 10px;
    width: 80px;
    height: 20px;
    font-size: 0.5rem; }
  .main_body_btn::after {
    right: 5px;
    top: 4px;
    zoom: 0.8; }
  .slide {

    img {
      overflow: hidden;
      height: auto;
      zoom: 0.3; } }

  .owl-dots {
    top: 300px;
    left: 10px; }
  .owl-dot {
    span {
      width: 4px;
      height: 4px; } }
  .owl-dot.active span {
    height: 6px;
    width: 6px; } }

@media (max-width: 1050px) {
  .modal_img {
    display: none; }
  .modal_form {
    width: 100%; } }
